export default {
	name: "chat",
	data() {
		return {
			height: "603px", //手机屏幕高度
			searchValue: "", //搜索值
			headerTop: "", //头部高度
		}
	},
	components: {

	},
	onReady() {
		uni.getSystemInfo({ //获取手机屏幕高度信息，让swiper的高度和手机屏幕一样高
			success: (res) => {
				console.log("res", res);
				this.height = res.screenHeight + 'px';
				this.headerTop = res.windowTop + 'px';
			}
		});
	},
	onNavigationBarButtonTap(e) {
		uni.showActionSheet({
			itemList: ['忽略未读提醒', '清空聊天列表', '提醒设置'],
			success: function(res) {
				console.log('选中了第' + (res.tapIndex) + '个按钮');
				if (res.tapIndex == 2) {
					uni.navigateTo({
						url: '/pagesA/settingChat/settingChat'
					});
				}
			},
			fail: function(res) {
				console.log(res.errMsg);
			}
		});
	},
	mounted() {

	},
	methods: {
		toChat() {
			uni.navigateTo({
				url: '/pages/im-chat/im-chat'
			});
		}
	}
}
